import axios, { AxiosInstance, AxiosResponse } from 'axios';
import { Document, AnalysisResponse, HealthCheckResponse } from '@/types/api';

// Backend API
export interface SupportedFormatsResponse {
  success: boolean;
  data: {
    formats: Record<string, string>;
    ocr_enabled: boolean;
    ocr_formats: string[];
  };
}

export interface ProcessorStatusResponse {
  success: boolean;
  data: {
    ocr_enabled: boolean;
    supported_formats: Record<string, string>;
    version: string;
  };
}

export interface DocumentProcessResult {
  success: boolean;
  message: string;
  data: {
    document_id: string;
    document_type: string;
    total_pages: number;
    total_text_length: number;
    processing_time: number;
    metadata: Record<string, any>;
    pages: Array<{
      page_number: number;
      width: number;
      height: number;
      has_images: boolean;
      image_count: number;
      text_blocks_count: number;
    }>;
    text_blocks: Array<{
      text: string;
      page_number: number;
      block_id: string;
      original_position?: [number, number, number, number];
      char_start: number;
      char_end: number;
      confidence: number;
      block_type: string;
      is_ocr: boolean;
    }>;
  };
}

class ApiService {
  private api: AxiosInstance;
  private baseURL: string;
  private displayURL: string;

  // API ( -> )
  private urlMappings: Record<string, string> = {
    'https://zhihh.github.io/DocuPrism/api': 'https://docuprism.zhihh.xyz',
    'https://docuprism.zhihh.xyz': 'https://docuprism.zhihh.xyz',
    'http://8.137.111.189:50200': 'http://8.137.111.189:50200',
    'http://localhost:8000': 'http://localhost:8000'
  };

  constructor() {
    // localStorageAPI
    this.displayURL = this.getApiDisplayUrl();
    this.baseURL = this.mapToRealUrl(this.displayURL);
    
    this.api = axios.create({
      baseURL: this.baseURL,
      timeout: 300000, // 5
      headers: {
        'Content-Type': 'application/json',
      },
    });

    // 
    this.api.interceptors.request.use(
      (config) => {
        console.log(`API: ${config.method?.toUpperCase()} ${config.url}`);
        return config;
      },
      (error) => {
        console.error('API:', error);
        return Promise.reject(error);
      }
    );

    // 
    this.api.interceptors.response.use(
      (response: AxiosResponse) => {
        console.log(`API: ${response.status} ${response.config.url}`);
        return response;
      },
      (error) => {
        console.error('API:', error);
        
        // 
        if (error.response) {
          const status = error.response.status;
          switch (status) {
            case 400:
              throw new Error('');
            case 404:
              throw new Error('API');
            case 422:
              throw new Error('');
            case 500:
              throw new Error('');
            case 503:
              throw new Error('');
            default:
              throw new Error(` (${status})`);
          }
        } else if (error.request) {
          throw new Error('');
        } else {
          throw new Error('');
        }
      }
    );
  }

  // API
  private mapToRealUrl(displayUrl: string): string {
    return this.urlMappings[displayUrl] || displayUrl;
  }

  // 
  private mapToDisplayUrl(realUrl: string): string {
    for (const [display, real] of Object.entries(this.urlMappings)) {
      if (real === realUrl) {
        return display;
      }
    }
    return realUrl;
  }

  private getApiDisplayUrl(): string {
    // 1. localStorage
    const storedApiUrl = localStorage.getItem('apiBaseUrl');
    if (storedApiUrl && storedApiUrl !== 'default') {
      return storedApiUrl;
    }

    // 2. 
    const envApiUrl = import.meta.env.VITE_API_BASE_URL;
    if (envApiUrl) {
      return this.mapToDisplayUrl(envApiUrl);
    }

    // 3. 
    return 'https://zhihh.github.io/DocuPrism/api';
  }

  // API
  updateBaseURL(newDisplayURL: string) {
    this.displayURL = newDisplayURL;
    this.baseURL = this.mapToRealUrl(newDisplayURL);
    this.api.defaults.baseURL = this.baseURL;
    localStorage.setItem('apiBaseUrl', newDisplayURL);
    console.log(`API: ${newDisplayURL}`);
    console.log(`API: ${this.baseURL}`);
  }

  // API ()
  getDisplayURL(): string {
    return this.displayURL;
  }

  getBaseURL(): string {
    return this.baseURL;
  }

  /**
   * 
   */
  async healthCheck(): Promise<HealthCheckResponse> {
    const response = await this.api.get<HealthCheckResponse>('/health');
    return response.data;
  }

  /**
   * 
   */
  async getStatus(): Promise<{ status: string; message: string }> {
    const response = await this.api.get<{ status: string; message: string }>('/');
    return response.data;
  }

  /**
   * 
   */
  async analyzeDocuments(documents: Document[]): Promise<AnalysisResponse> {
    // 
    if (!documents || documents.length === 0) {
      throw new Error('');
    }

    if (documents.length < 2) {
      throw new Error('2');
    }

    // 
    for (const doc of documents) {
      if (!doc.content || doc.content.trim().length === 0) {
        throw new Error(`${doc.documentId}`);
      }
      if (doc.content.length > 50000) {
        throw new Error(`${doc.documentId}50000`);
      }
    }

    console.log(`${documents.length}...`);
    
    const response = await this.api.post<AnalysisResponse>('/api/v2/analyze', documents);
    
    console.log(`${response.data.total_count}`);
    
    return response.data;
  }

  /**
   * API
   */
  async getApiDocs(): Promise<string> {
    const response = await this.api.get('/docs', {
      headers: {
        'Accept': 'text/html',
      },
    });
    return response.data;
  }

  /**
   * 
   */
  async testConnection(): Promise<boolean> {
    try {
      await this.healthCheck();
      return true;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }

  /**
   * 
   */
  async getSupportedFormats(): Promise<SupportedFormatsResponse> {
    const response = await this.api.get<SupportedFormatsResponse>('/api/v2/supported-formats');
    return response.data;
  }

  /**
   * 
   */
  async getProcessorStatus(): Promise<ProcessorStatusResponse> {
    const response = await this.api.get<ProcessorStatusResponse>('/api/v2/processor-status');
    return response.data;
  }

  /**
   * 
   */
  async uploadAndProcessDocument(file: File, documentId?: string): Promise<DocumentProcessResult> {
    const formData = new FormData();
    formData.append('file', file);
    if (documentId) {
      formData.append('document_id', documentId);
    }

    const response = await this.api.post<DocumentProcessResult>(
      '/api/v2/upload-document',
      formData,
      {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        timeout: 300000, // 5
      }
    );

    return response.data;
  }

  /**
   * 
   */
  async uploadAndAnalyzeDocuments(
    files: File[], 
    threshold: number = 0.7, 
    method: string = 'semantic'
  ): Promise<AnalysisResponse> {
    if (!files || files.length === 0) {
      throw new Error('');
    }

    if (files.length < 2) {
      throw new Error('2');
    }

    const formData = new FormData();
    
    // 
    files.forEach((file) => {
      formData.append('files', file);
    });
    
    // 
    formData.append('threshold', threshold.toString());
    formData.append('method', method);

    console.log(`${files.length}...`);

    const response = await this.api.post(
      '/api/v2/upload-and-analyze',
      formData,
      {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        timeout: 600000, // 10
      }
    );

    console.log(`${response.data.total_count}`);
    
    // 
    const backendData = response.data;
    
    // DuplicateResult
    const convertedData = backendData.data?.map((item: any) => ({
      documentId1: item.doc1_id || item.documentId1,
      page1: item.page1 || 1,
      chunkId1: 1,
      content1: item.content1,
      prefix1: '',
      suffix1: '',
      documentId2: item.doc2_id || item.documentId2,
      page2: item.page2 || 1,
      chunkId2: 1,
      content2: item.content2,
      prefix2: '',
      suffix2: '',
      reason: item.reason,
      score: item.similarity || item.score,
      category: item.category || 1
    })) || [];
    
    return {
      success: backendData.success !== false,
      message: backendData.message || '',
      data: convertedData,
      total_count: backendData.total_count || convertedData.length,
      processing_time: backendData.processing_time || 0
    };
  }

  /**
   * APIURL
   */
  setBaseURL(url: string): void {
    this.baseURL = url;
    this.api.defaults.baseURL = url;
  }
}

// 
const apiService = new ApiService();

export default apiService;